                <!-- <div v-for="(item,index) in tablist" :key="index">
                    <div class="tab1" @click="clickedtab(index,item)">
                        <span>{{item}}</span>
                        <div class="underline" :class="{clicked:index==currenttabindex}"></div>
                    </div>
                </div> -->
            </div>
            <div class="list">
                <div class="tablewrap">
                    <table>
                        <tr class="header">
                            <td v-show="index>3&&paginatedata[0][index]!=''" width="150px" v-for="(item,index) of regisinfo" :key="index">{{item}}</td>
                        </tr>
                        <tr class="listitem" v-for="(item,upindex) in paginatedata" :key="upindex">
                            <td v-for="(listitem,index)  of item" :key="index" @click="openmemberinfo(upindex)" v-show="index>3">{{listitem}}</td>

                        </tr>
                    </table>
