<script>
import Headerbar from '../components/Header'
import Actblock from '../components/Actblock'
import Searchbar from '../components/Searchbar'
// import Iconbtn from '../components/Iconbtn'
import axios from 'axios'
import Actmemberinfopage from '../components/ActMemberinfo'
export default {
    data() {
        return {
            isclick: false,
            tablist: ['全部', '已報名', '已取消'],
            currenttabindex: 0,
            regisinfo: [],
            listinfo: [],
            testlist: [],
            isopen: false,
            clickmemberid: 0,
            propsisopen: false,
            currentstatus: '全部',
            keyword: '',
            loadingcircle: false,
            pagenumber: 0,
            pagesize: 7,
            currenteventinfo: null
        }
    },
    mounted() {
        var storeeventinfo = window.sessionStorage.getItem('storeeventinfo');
        this.currenteventinfo = JSON.parse(storeeventinfo);
        let url = "https://script.google.com/macros/s/AKfycbwOsDW-9VzsJe18DqVvbwnyuQAod8JFxXZPNv_Jt9zjXx5lb3DaJhty7axgaM7Euwc/exec"

        axios({
                method: 'get',
                url: url,
            })
            .then((response) => {

                this.regisinfo = response.data.res[0]
                let newlist = response.data.res

                this.listinfo = newlist.filter(item => {
                    if (item[0] == this.currenteventinfo[0]) {
                        return item
                    }
                })

            })
            .catch((error) => {
                console.error(error)
            })
        // console.log(this.listinfo)
        // setInterval(() => {
        //     this.loadingcircle = false
        //     document.body.style.overflow = 'visible'
        // }, 1600)
    },
    methods: {
        clickedtab(index, status) {
            this.currentstatus = status;
            this.currenttabindex = index;
        },
        openmemberinfo(id) {
            this.isopen = true;
            this.clickmemberid = id;
            document.body.scrollTop = document.documentElement.scrollTop = 0;
            this.propsisopen = true;
        },
        closememberifo() {
            this.isopen = false;
            document.body.style.overflow = 'visible'
        },
        search(val) {
            this.keyword = val;
            console.log(val)

        },
        uppage() {
            this.pagenumber--
        },
        nextpage() {
            this.pagenumber++
        },
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        }

    },
    computed: {
        filtermemberlist: {
            get() {
                return this.listinfo.filter(item => {
                    console.log(item[4])
                    if (this.keyword == "") {
                        return this.listinfo;
                    } else if (item[4].search(this.keyword) != -1) {
                        return item
                    }
                })
            }

        },
        pagecount() {
            let l = this.filtermemberlist.length;
            let s = this.pagesize;
            return Math.floor(l / s)
        },
        paginatedata() {
            const start = this.pagenumber * this.pagesize
            const end = start + this.pagesize;

            return this.filtermemberlist.slice(start, end)
        }
    },
    components: {
        Headerbar,
        Actblock,
        Searchbar,
        Actmemberinfopage
    }
}
</script>

